import { ReactNode } from 'react';
import { Link } from 'react-router-dom';
import useConnectionDetails from '../../../hooks/connection/useConnectionDetails';
import FallbackImg from '../../FallbackImg/FallbackImg';
import LoadingParagraph from '../../Loaders/LoadingParagraph/LoadingParagraph';

const PersonCard = ({
  name,
  dotYouId,
  href,
  checked,
  className,
  children,
  onClick,
}: {
  name?: string;
  dotYouId: string;
  href?: string;
  checked?: boolean;
  className: string;
  children?: ReactNode;
  onClick?: React.MouseEventHandler<HTMLDivElement>;
}) => {
  const Wrapper = ({ children }) => (href ? <Link to={href}>{children}</Link> : <>{children}</>);
  const { data: connectionDetails, isLoading } = useConnectionDetails({ dotYouId }).fetch;

  if (isLoading) {
    return <LoadingParagraph className={`aspect-square ${className}`} />;
  }

  const fullName =
    name ??
    (connectionDetails
      ? `${connectionDetails.name?.givenName ?? ''} ${connectionDetails.name?.surname ?? ''}`
      : undefined);

  const getInitials = () => {
    const splittedName = fullName?.split(' ');
    if (splittedName?.length >= 2 && splittedName[0].length) {
      return splittedName[0][0] + splittedName[1][0] + '';
    }
    const splittedDomain = dotYouId?.split('.');
    if (splittedDomain?.length >= 2) {
      return splittedDomain[0][0] + splittedDomain[1][0] + '';
    }
    return '--';
  };

  return (
    <div className={className}>
      <Wrapper>
        <div
          className={`h-full rounded-md border border-gray-200 border-opacity-60 bg-white transition-colors dark:border-gray-800 dark:bg-gray-800 ${
            checked
              ? 'border-4 border-indigo-500 dark:border-indigo-700'
              : checked === false
              ? 'border-4'
              : ''
          }`}
          onClick={onClick}
        >
          <div className="aspect-square">
            {connectionDetails?.profileImage?.url ? (
              <figure className={'relative overflow-hidden'}>
                <img
                  src={connectionDetails.profileImage.url}
                  width={connectionDetails.profileImage.naturalSize.width}
                  height={connectionDetails.profileImage.naturalSize.height}
                  className="aspect-square w-full"
                />
              </figure>
            ) : (
              <FallbackImg initials={getInitials()} />
            )}
          </div>
          <div className="p-2">
            <h2 className="font-thiner mb-6 dark:text-white">{fullName ?? dotYouId}</h2>
            {children}
          </div>
        </div>
      </Wrapper>
    </div>
  );
};

export default PersonCard;
